<template>
  <div class="detail">
    <div class="header">
      <router-link to="/phoneBook" tag="div" class="iconfont back-icon">&#xe663;</router-link>
    </div>
    <div class="image">
      <div class="info">
        <img :src="getImageUrl(phoneBook)" @click="showGalleryClick">
        <input type="hidden" v-model="phoneBook.image">
      </div>
      <div class="action">
        <button @click="imageClick">修改头像</button>
        <input type="file" name="file" accept="image/png,image/gif,image/jpeg,image/jpg"  @change="upload" ref="file">
      </div>
    </div>
    <div class="userInfo">
      <div class="item border-bottom">
        <span>姓名</span>
        <input type="text"  v-model="phoneBook.name">
      </div>
      <div class="item border-bottom">
        <span>手机号码</span>
        <input  type="text" v-model="phoneBook.phoneNumber">
      </div>
      <div class="item border-bottom">
        <span>座机电话号码</span>
        <input  type="text" v-model="phoneBook.teleNumber">
      </div>
      <div class="item border-bottom">
        <span>工作单位地址</span>
        <textarea v-model="phoneBook.workAddress"></textarea>
      </div>
      <div class="item border-bottom">
        <span>家庭地址</span>
        <textarea  v-model="phoneBook.homeAddress"></textarea>
      </div>
      <div class="item border-bottom">
        <span>备注</span>
        <textarea class="border-bottom" v-model="phoneBook.remark"></textarea>
      </div>
    </div>
    <div class="action">
      <div class="item border-bottom">
        <a @click="updateInfo">保存</a>
      </div>
      <div class="item border-bottom">
        <a @click="deleteInfo">删除</a>
      </div>
    </div>
    <gallery
      v-show="showGallery"
      :galleryImage="galleryImage"
      @hideGalleryClick="hideGalleryClick"></gallery>
  </div>
</template>

<script>

   import Gallery from '@/pages/common/Gallery'
   import PubSub from 'pubsub-js'
   import axios from 'axios'
   import {getServerUrl} from '@/config/sys.js'
   export default {
       name: "Detail",
       data(){
         return{
           phoneBook:{},
           showGallery:false,
           galleryImage:''
         }
       },
       components:{
         Gallery
       },
       methods:{
         showGalleryClick(){
           this.galleryImage=getServerUrl("image/"+this.phoneBook.image)
           this.showGallery=true
         },
         hideGalleryClick(){
           this.showGallery=false
         },
         imageClick(){
            this.$refs.file.click();
         },
         upload(e){
           let file = e.target.files[0];
           let param = new FormData(); //创建form对象
           param.append('file',file,file.name);//通过append向form对象添加数据
           param.append('chunk','0');//添加form表单中其他数据
           console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
           let url=getServerUrl('uploadImage');
           let token=window.localStorage.getItem('token')
           let config = {
             headers:{'Content-Type':'multipart/form-data','token':token}
           };  //添加请求头
           axios.post(url,param,config)
             .then(response=>{
               console.log(response.data)
               if(response.data.code==0){
                 console.log(response.data.data.src);
                 this.phoneBook.image=response.data.data.title
               }
             })
         },
         deleteInfo(){
           if(confirm('您确定要删除这条记录吗')){
             let url=getServerUrl('phoneBook/delete');
             let token=window.localStorage.getItem('token')
             axios.defaults.headers.common['token'] = token;
             axios.get(url,{
               params:{
                 id:this.$route.params.id
               }
             }).then(response=>{
               if(response.data.code==0){
                 alert("删除成功")
                 PubSub.publish('refreshPhoneBook','');
                 this.$router.replace('/phoneBook')
               }else{
                 alert(response.data.msg)
               }
             }).catch(error=>{
               alert(error+'-请联系管理员')
             })
           }
         },
         updateInfo(){
           let url=getServerUrl('phoneBook/save');
           let token=window.localStorage.getItem('token')
           axios.defaults.headers.common['token'] = token;
           if(this.phoneBook.name==null || this.phoneBook.name.trim()==''){
             alert("姓名不为空！")
             return;
           }
           if(this.phoneBook.phoneNumber==null || this.phoneBook.phoneNumber.trim()==''){
             alert("手机号码不为空！")
             return;
           }
           if(!(/^1[3456789]\d{9}$/.test(this.phoneBook.phoneNumber))){
             alert("手机号码有误，请重填");
             return;
           }
           if(this.phoneBook.teleNumber!=null && this.phoneBook.teleNumber.trim()!='' && !/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(this.phoneBook.teleNumber)){
             alert('固定电话有误，请重填');
             return false;
           }
           axios.post(url,{"id":this.phoneBook.id,"name":this.phoneBook.name,"phoneNumber":this.phoneBook.phoneNumber,"teleNumber":this.phoneBook.teleNumber,"workAddress":this.phoneBook.workAddress,"homeAddress":this.phoneBook.homeAddress,"image":this.phoneBook.image,"remark":this.phoneBook.remark,"image":this.phoneBook.image})
             .then(response=>{
               if(response.data.code==0){
                 alert("修改成功")
                 PubSub.publish('refreshPhoneBook','');
                 this.$router.replace('/phoneBook')
               }else{
                 alert(response.data.msg)
               }
             }).catch(error=>{
             alert(error)
           })
         },
         getImageUrl(image){
           return getServerUrl("image/"+image);
         },
         getInfo(){
           let url=getServerUrl('phoneBook/findById');
           let token=window.localStorage.getItem('token')
           axios.defaults.headers.common['token'] = token;
           axios.get(url,{
             params:{
               id:this.$route.params.id
             }
           }).then(response=>{
               this.phoneBook=response.data.phoneBook;
               console.log(this.phoneBook)
             }).catch(error=>{
             alert(error+'-请联系管理员')
           })
         }
       },
       mounted() {
         this.getInfo();
       }
   }

</script>

<style lang="stylus" scoped>

  @import "~@/assets/styles/varibles.styl"
  .border-bottom
    &:before
      border-color: #D3D3D3

  .detail
    .header
      position:fixed
      top:0
      overflow:hidden
      height:$headerHeight
      line-height:$headerHeight
      text-align :left
      padding-left:.2rem
      padding-top: .1rem
      background-color: #D3D3D3
      width: 100%
      z-index:10
      opacity:0.8


    .image
      margin-top:$headerHeight
      text-align: center
      width: 90%
      height: 2.5rem
      .info
        img
          float: left
          border-radius: 0.5rem
          overflow: hidden
          padding: 0.3rem
          width: 2rem
          height: 2rem
      .action
        padding-top: 1.55rem
        text-align: left
        input
          display: none
        button
          background-color: #4CAF50; /* Green */
          border: none;
          color: white;
          padding: .15rem .15rem;
          text-align: center;
          text-decoration: none;
          display: inline-block;
          font-size: 16px;
          border-radius: .2rem

   .userInfo
    padding-left:0.3rem
    padding-top: 0rem
    .item
     padding-top: .3rem
     padding-bottom: .1rem
     span
      display: block
      font-weight: bold
     input
      padding-top: .1rem
      width: 100%
     textarea
       padding-top: .1rem
       width: 100%

   .action
    padding-top: .5rem
    padding-bottom: 2rem
    .item
     width: 100%
     text-align: center
     padding-top: .3rem
     padding-bottom: .3rem
     a
      font-size:.4rem
      color: #576B97


</style>



