<template>
  <div>
    <phone-book-header></phone-book-header>
    <phone-book-search
      :phoneBooks="phoneBooks"></phone-book-search>
    <phone-book-list
      :phoneBooks="phoneBooks"
      :letter="letter"></phone-book-list>
    <phone-book-alphabet
      :phoneBooks="phoneBooks"
      @change="handleLetterChange"></phone-book-alphabet>
  </div>
</template>

<script>
  import PhoneBookHeader from './components/Header'
  import PhoneBookSearch from './components/Search'
  import PhoneBookList from './components/List'
  import PhoneBookAlphabet from './components/Alphabet'
  import axios from 'axios'
  import PubSub from 'pubsub-js'
  import {getServerUrl} from '@/config/sys.js'

  export default {
    name: "PhoneBook",
    data(){
      return{
        phoneBooks:{},
        letter: ''
      }
    },
    components:{
      PhoneBookHeader,
      PhoneBookSearch,
      PhoneBookList,
      PhoneBookAlphabet
    },
    methods:{
      refreshToken() {
        let url=getServerUrl('refreshToken');
        let token=window.localStorage.getItem('token')
        axios.defaults.headers.common['token'] = token;
        axios.get(url,{})
          .then(response=>{
            if(response.data.code==0){
              window.localStorage.setItem('token',response.data.msg);
              console.log('token刷新成功'+response.data.msg)
            }
          }).catch(error=>{
          alert(error+'-请联系管理员')
        })
      },
      getPhoneBooks(){
        let url=getServerUrl('phoneBook/loadAll');
        let token=window.localStorage.getItem('token')
        axios.defaults.headers.common['token'] = token;
        axios.get(url)
          .then(response=>{
            this.phoneBooks=response.data.data;
            console.log(this.phoneBooks)
          }).catch(error=>{
          alert(error+'-请联系管理员')
        })

        // url = getServerUrl(`phoneBook/avatar/trytouch`);

        // axios.post(url)
      },
      handleLetterChange(letter){
        this.letter=letter
      }
    },
    mounted() {
      setInterval(this.refreshToken,1000*60*10)  // 10分钟刷新一次token

      this.getPhoneBooks();  // 加载所有电话簿信息

      PubSub.subscribe('refreshPhoneBook',(msg,data)=>{
        console.log('收到消息refreshPhoneBook')
        this.getPhoneBooks();

      })
    }
  }
</script>

<style lang="stylus" scoped>
   *
    touch-action: pan-y
</style>



