<template>
    <ul class="list">
      <li
        class="item"
        v-for="(item,key) of letters"
        :key="item"
        :ref="item"
        @click="handleLetterClick"
        @touchstart.prevent="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
        >{{item}}
      </li>
    </ul>
</template>

<script>
    export default {
        name: "Alphabet",
        props:{
          phoneBooks:Object
        },
        computed:{
          letters(){
            const letters = []
            for(let i in this.phoneBooks){
              letters.push(i)
            }
            return letters
          }
        },
        data(){
          return {
            touchStatus: false,
            startY:0
          }
        },
        updated(){
          this.startY=this.$refs['A'][0].offsetTop
        },
        methods:{
          handleLetterClick(e){
            console.log('click')
            this.$emit('change',e.target.innerText);
          },
          handleTouchStart(e){
              this.touchStatus=true
            if(this.touchStatus){
              if(this.timer){
                clearTimeout(this.timer)
              }
              this.timer=setTimeout(()=>{
                if(this.touchStatus){
                  if(this.touchStatus){
                    const touchY=e.touches[0].clientY-79
                    const index=Math.floor((touchY-this.startY)/20)
                    if(index>=0 && index<this.letters.length){
                      this.$emit('change',this.letters[index]);
                    }
                  }
                }
              },16)
            }
            console.log("start")
          },
          handleTouchMove(e){
            if(this.touchStatus){
              if(this.timer){
                clearTimeout(this.timer)
              }
              this.timer=setTimeout(()=>{
                if(this.touchStatus){
                  if(this.touchStatus){
                    const touchY=e.touches[0].clientY-79
                    const index=Math.floor((touchY-this.startY)/20)
                    if(index>=0 && index<this.letters.length){
                      this.$emit('change',this.letters[index]);
                    }
                  }
                }
              },16)
            }
          },
          handleTouchEnd(){
            console.log("end")
            this.touchStatus=false
          }
        }
    }
</script>

<style lang="stylus" scoped>

  @import "~@/assets/styles/varibles.styl"
  .list
    display: flex
    flex-direction: column
    justify-content: center
    position: absolute
    top: 1.58rem
    right: 0
    bottom: 0
    width: .4rem
    .item
      line-height: .4rem
      text-align: center




</style>



