<template>
  <div>
    <div class="login-logo circle text-center">通讯录</div>

    <form name="forms" onsubmit="return checkreg()" action="" method="post" class="login-cont">
      <ul>
        <li><input type="text" v-model="userName" placeholder="请输入用户名：" /></li>
        <li>
          <span id="box"><input type="password" v-model="password"  name="password" placeholder="请输入密码："/></span>
        </li>
      </ul>
      <a href="javascript:;" class="button text-center block" @click="submit()">登 录</a>
      <p class="errorInfo">{{errorInfo}}</p>
    </form>
    <p class="copyright">Copyright © 2012-2019 <a href="http://www.java1234.com">www.java1234.com</a></p>
  </div>
</template>

<script>
  import axios from 'axios'
  import {getServerUrl} from '@/config/sys.js'
  export default {
    name: "Login",
    data(){
      return{
        userName:'',
        password:'',
        errorInfo:''
      }
    },
    methods:{
      submit(){

        let url=getServerUrl('login');
        if(this.userName.trim()==''){
          this.errorInfo="用户名不为空！"
          return;
        }
        if(this.password.trim()==''){
          this.errorInfo="密码不为空！"
          return;
        }
        axios.post(url,{"userName":this.userName,"password":this.password})
          .then(response=>{
            if(response.data.code==0){
              window.localStorage.setItem('token',response.data.token);
              console.log(this)
              this.$router.replace('/phoneBook')
            }else{
              this.errorInfo=response.data.msg;
            }
          }).catch(error=>{
          this.errorInfo=error;
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>


  .login-logo
    width: 3rem
    height: 3rem
    background: #10a78e
    margin: 1rem auto 0.5rem
    color: #fff
    line-height: 3rem
    font-size: 0.8rem

  .circle
    border-radius: 50%
    overflow: hidden

  .text-center
    text-align: center

  .login-cont
      margin: 1rem 0.8rem
      line-height: 0.4rem
  .login-cont li
      border-bottom: 1px solid #ebebeb
      height: 1rem
      padding: 0rem
      padding-top: 0.3rem
      position: relative
      overflow: hidden
  .login-cont li input
      border: none
      line-height: 0.8rem
      width: 100%
      outline: 0
     .button
       background: #10a78e
       color: #fff
       border-radius:1rem
       height: 1rem
       line-height: 1rem
       font-size: 0.6rem
       margin-top: 1rem
       padding: 0
     .text-center
       text-align: center
     .block
       display: block
     .errorInfo
        color: red
        padding-top: .3rem

  .copyright
    position: absolute
    padding-top: 0rem
    padding-left: 1rem
    font-size: 0.3rem



</style>



