<template>
    <div>
      <div class="search">
        <input v-model="keyword" class="search-input" type="text" placeholder="搜索"/>
      </div>
      <div class="search-content" ref="search" v-show="keyword">
          <router-link class="item"
               :to="`/detail/${item.id}`"
               tag="div"
               v-for="item of list"
               :key="item.id">
              <img :src="getImageUrl(item)">
              <div class="content border-bottom">{{item.name}}</div>
          </router-link>
          <div class="item center" v-show="!list.length">
            没找到匹配数据
          </div>


      </div>

    </div>
</template>

<script>

   import Bscroll from 'better-scroll'
   import {getServerUrl} from '@/config/sys.js'

    export default {
        name: "Search",
        props:{
          phoneBooks:Object
        },
        data(){
          return {
            keyword:'',
            list: [],
            timer: null
          }
        },
        methods:{
          getImageUrl(image){
            // return getServerUrl(`phoneBook/avatar/${image.imageName}?imageName=${image.imageName}`);
            // return image;
            return getServerUrl(`image/${image.imageName}`);
          }
        },
        watch:{
          keyword(){
            if(this.timer){
              clearTimeout(this.timer)
            }
            if(!this.keyword){
              this.list=[]
              return
            }
            this.timer=setTimeout(()=>{
              const result=[]
              for(let i in this.phoneBooks){
                this.phoneBooks[i].forEach((value)=>{
                  if(value.name.indexOf(this.keyword)>-1){
                    result.push(value)
                  }
                })
              }
              this.list=result
            },100)
          }
        },
        mounted() {
          this.scroll=new Bscroll(this.$refs.search,{click: true})
        }
    }
</script>

<style lang="stylus" scoped>

  @import "~@/assets/styles/varibles.styl"
  .border-bottom
    &:before
      border-color: #D3D3D3

  .search
    padding .1rem .1rem
    height:.72rem
    background :$bgColor
    text-align: center
    .search-input
      box-sizing :border-box
      width:100%
      height:.62rem
      line-height:.62rem
      text-align:center
      border-radius: .06rem
      color:#666
      padding :0 .1rem
  .search-content
    z-index: 99
    overflow: hidden
    position: absolute
    top: 1.74rem
    left: 0
    right: 0
    bottom: 0
    background: #eee
    .item
      line-height: .76rem
      margin: .2rem
      img
        float:left
        width: 0.8rem
        height: 0.8rem
        border-radius: 0.2rem
      .content
        padding-left: 1rem
        font-size: .4rem
        padding-bottom: .3rem
    .center
      text-align: center


</style>



